button{
    cursor:pointer;
    display: flex;
    align-items: center;
    justify-content: center;

    &.btn {
    margin-top: 24px;
    margin-bottom: 24px;
    float: right;
    box-shadow: none;
    background-color: $-red;
    font-family: $-default-font;
    transition: all .4s ease;
    -moz-transition: all .4s ease;
    box-shadow: 0 4px 16.5px rgba(247, 53, 88, .75);
    height: 36px;
    padding: 0 16px;
    color: #fff;
    letter-spacing: .5px;
    line-height: 36px;
    border-radius: 7px;
    cursor: pointer;

        &:hover {
            transform: scale(1.075);
            box-shadow: 0 4px 16.5px 0 rgba(247, 53, 88, .65);
        }

        &:active {
            transform: scale(1);
            box-shadow: 0 4px 16.5px 0 rgba(247, 53, 88, 1);
        }
    }

    &.circle {
        width: 36px;
        height: 36px;
        border-radius: 50%;
        background: $-red;
        color: #fff;
        transition: all .4s ease;
        -moz-transition: all .4s ease;
        box-shadow: 0 4px 16.5px rgba(247, 53, 88, .75);

        & .icon {
            color: #fff!important;
        }

        &:hover {
            transform: scale(1.075);
            box-shadow: 0 4px 16.5px 0 rgba(247, 53, 88, .65);
        }

        &:active {
            transform: scale(1);
            box-shadow: 0 4px 16.5px 0 rgba(247, 53, 88, 1);
        }
    }
}