/*DEFINE*/
:root {
    --shadow-0: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
    --shadow-1: 0 6px 14px 0 rgba(0, 0, 0, 0.22);

    /*COLOR*/
    --color-50: #ede7f6;
    --color-100: #d1c4e9;
    --color-200: #b39ddb;
    --color-300: #9575cd;
    --color-400: #7e57c2;
    --color-500: #673ab7;
    --color-600: #5e35b1;
    --color-700: #512da8;
    --color-800: #4527a0;
    --color-900: #311b92;
    --color-A100: #b388ff;
    --color-A200: #7c4dff;
    --color-A400: #651fff;
    --color-A700: #6200ea;
}

.splash_screen {
    width: 100%;
    height: 100%;
    background-color: #fff;
    position: fixed;
    z-index: 9999999999;
    display: flex;
    align-items: center;
    justify-content: center;
}

.splash_screen img {
    width: 64pt;
    animation: splash_screen_logo 1s ease;
    -moz-animation: splash_screen_logo 1s ease;
    -webkit-animation: splash_screen_logo 1s ease;
}

@keyframes splash_screen_logo {
    0% {
        transform: scale(.9);
        filter: grayscale(1);
    }

    50% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(1);
        filter: grayscale(0);
    }
}

@-moz-keyframes splash_screen_logo {
    0% {
        transform: scale(.9);
        filter: grayscale(1);
    }

    50% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(1);
        filter: grayscale(0);
    }
}

@-webkit-keyframes splash_screen_logo {
    0% {
        transform: scale(.9);
        filter: grayscale(1);
    }

    50% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(1);
        filter: grayscale(0);
    }
}

.viewTitle {
    font-size: 32px;
    left: 0;
    text-align: left;
}

.page_header {
    display: none;

    @include mobile {
        display: block;
        margin-top: 48px;
        width: 80%;
        max-width: 520px;
        padding-bottom: 18px;
        margin: auto;
        border-bottom: 1px solid #e3e3e5;

        & h1 {
            font-size: 40px;
            font-weight: 700;
            margin: 0;
            margin-top: 48px;
            color: $-black;
        }
    }

    @include tablet {
        display: block;
        margin-top: 48px;
        width: 80%;
        max-width: 520px;
        padding-bottom: 18px;
        margin: auto;
        border-bottom: 1px solid #e3e3e5;

        & h1 {
            font-size: 40px;
            font-weight: 700;
            margin: 0;
            margin-top: 48px;
            color: $-black;
        }
    }
}


/* The Modal (background) */
.modal {
    display: none;
    /* Hidden by default */
    position: fixed;
    /* Stay in place */
    z-index: 1;
    /* Sit on top */
    padding-top: 100px;
    /* Location of the box */
    left: 0;
    top: 0;
    width: 100%;
    /* Full width */
    height: 100%;
    /* Full height */
    overflow: auto;
    /* Enable scroll if needed */
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.6);
    /* Black w/ opacity */
    z-index: 4;
}

/* Modal Content */
.modal-content {
    background-color: #fefefe;
    margin: auto;
    padding: 16px;
    border: 1px solid #888;
    width: 400px;
    border-radius: 13px;
    text-align: center;
}




/*INPUT*/
input[type="text"]:focus {}

.input {
    width: 100%;
    height: 32px;
    background: transparent;
    margin-top: 30px;

    .input-icon {
        width: 48px;
        height: 32px;
        border-right: 1px solid #23374555;
        float: left;

        i {
            font-size: 26px;
            line-height: 40px;
            color: #233745;
            margin-top: -4px;
        }
    }

    input {
        color: #233745;
        font-weight: 700;
        margin-left: 16px;
        height: 32px;
        float: left;
        width: calc(100% - 100px);
        border: none;
        outline: none;
    }

    input::placeholder {
        font-weight: 700;
        color: #23374555;
    }
}

/*BUTTON*/
button {
    outline: none;
    background: transparent;
    border: none;
}

/*PAGE*/
.main-bg {
    //LOGIN AND SIGNUP BACKGROUND
    background: url('./resources/image/undraw_personalization_triu.svg') no-repeat;
    background-position: center center;
    background-size: 50%;
    height: 100%;
    width: 100%;
    text-align: center;
}



/*FORM*/
form {
    background-color: #fff;
    box-shadow: var(--shadow-0);
    border-radius: 8px;
}

.section_scrollable {
    overflow: auto;
    height: 100vh;
    max-height: 100vh;

    & .section.confess {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding-top: 0;
    
        @include mobile {
            margin: 0;
            padding: 0;
        }
    
        @include tablet {
            margin: 0;
            padding: 0;
        }
    }
}

.section-line {
    width: 170px;
    text-align: center;
    margin: auto;

    & .line {
        border-bottom: 1px solid #23374575;
    }

    & .circle {
        width: 49px;
        height: 20px;
        background-color: #fff;
        border-radius: 50%;
        margin: auto;
        margin-top: -26px;

        & .icon-contain {
            width: 25px;
            height: 25px;
            margin: 12.5px;
            background-color: #23374575;
            border-radius: 50%;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;

            & .icon {
                fill: #fff;
                color: #fff!important;
                font-size: 13px;
            }

            & a {
                color: #233745;
                font-weight: 700;
                font-size: 11px;
            }
        }
    }
}

//CONFESS FORM
//#region
.confess-form {
    position: relative;
    width: 694px;
    height: auto;
    margin: auto;
    background-color: #fff;
    box-shadow: var(--shadow-0);
    border-radius: 13px;
    padding: 16px 64px 32px 64px;
    text-align: left;

    @include mobile {
        padding: 16px 64px 0px 64px;
        width: 335px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        height: auto;
    }

    @include tablet {
        padding: 16px 64px 0px 64px;
        width: 335px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        height: auto;
    }

    & div {
        & a#error-confess {
            font-size: 14px;
            text-align: left;
            color: $-red;
        }
    }

    & textarea {
        width: 100%;
        min-height: 53px;
        border: none;
        outline: none;
        font-size: 16px;
        font-weight: 500;
        border-bottom: 1px solid #23374540;
        user-select: text;
        -moz-user-select: text;
        -webkit-user-select: text;
        //resize: none;
        line-height: 1.3053;
        letter-spacing: -.003em;

        @include mobile {
            min-height: 120px;
        }

        @include tablet {
            min-height: 120px;
        }

        &::placeholder {
            transition: all .4s ease;
            color: $-gray-1;
        }

        &:focus::placeholder {
            color: transparent;
        }
    }

    & .confess_upload_image_btn_m {
        width: 100%;
        height: 36px;
        background: #000;
        border-radius: 7px;
        margin-top: 32px;
        cursor: pointer;
        transition: all .3s;
        box-shadow: 0 2px 15px rgba(0, 0, 0, .25);
        display: none;
        width: auto;
        padding: 0 16px;

        @include mobile {
            display: flex;
        }

        @include tablet {
            display: flex;
        }

        & .inputfile {
            width: 0.1px;
            height: 0.1px;
            opacity: 0;
            overflow: hidden;
            position: absolute;
            z-index: -1;
        }

        .inputfile+label {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;

            & span {
                font-size: 20px;
                //color: #233745;
                color: $-dark-3;
            }

            & a {
                font-size: 13px;
                margin-left: 16px;
                color: #414141;
                margin-top: 1px;
            }
        }
    }

    & h4 {
        font-weight: 600;
        margin-bottom: 24px;
        font-size: 36px;
        line-height: 1.07143;
        letter-spacing: -.005em;
        color: #233745;
        text-align: center;

        @include mobile {
            margin-bottom: 32px;
            font-size: 20px;
        }

        @include tablet {
            margin-bottom: 32px;
            font-size: 20px;
        }
    }

    & a {
        color: $-dark;
        font-size: 20px;
        font-weight: 500;

        @include mobile {
            font-size: 16px;
        }

        @include tablet {
            font-size: 16px;
        }
    }

    & .confess_image_container {
        display: flex;
        width: 100%;
        position: relative;
        margin: 16px 0 48px 0;
        align-items: center;
        justify-content: center;
        display: none;

        @include mobile {
            margin: 0;
            margin-top: 16px;
        }

        @include tablet {
            margin: 0;
            margin-top: 16px;
        }

        & .image {
            width: 75%;
            height: auto;
            background-color: #888;
            border-radius: 16px;
            background-position: center center;
            background-size: cover;
            background-repeat: no-repeat;
            transition: all .4s ease;
            z-index: 1;

            @include mobile {
                width: 100%;
            }

            @include tablet {
                width: 100%;
            }

            &:after {
                content: "";
                display: block;
                padding-bottom: 120%;
            }
        }

        & .image_shadow {
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            position: absolute;
            width: 70%;
            height: 100%;
            overflow: hidden;
            display: flex;
            left: 50%;
            transform: translate(-50%, 0);
            bottom: -15px;
            z-index: 0;
            border-radius: 20px;
            filter: blur(15px);

            @include mobile {
                width: 80%;
            }

            @include tablet {
                width: 80%;
            }

            & .shadow_image {
                margin-top: 3rem;
                object-fit: cover;
                width: 100%;
                height: 100%
            }
        }
    }

    & .confess_submit_container {
        display: flex;
        width: 100%;
        position: relative;
        margin-top: 16px;

        @include mobile {
            flex-direction: column;
        }

        @include tablet {
            flex-direction: column;
        }

        & .confess_background_select_container {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 24px;

            @include mobile {
                margin: 0;
            }

            @include tablet {
                margin: 0;
            }

            & .confess_background_item,
            .confess_upload_image_btn {
                width: 36px;
                height: 36px;
                background: #000;
                border-radius: 7px;
                margin: 0 8px;
                cursor: pointer;
                transition: all .3s;
                box-shadow: 0 2px 15px rgba(0, 0, 0, .25);

                &.confess_background_item:nth-of-type(1) {
                    background: #fff;
                }

                &.confess_background_item:nth-of-type(2) {
                    background: url(./resources/postBackground/0.jpg); 
                    background-size: cover; 
                    background-position: center center; 
                    background-repeat: no-repeat;
                }

                &.confess_background_item:nth-of-type(3) {
                    background: url(./resources/postBackground/1.jpg); 
                    background-size: cover; 
                    background-position: center center; 
                    background-repeat: no-repeat;
                }

                &.confess_background_item:nth-of-type(4) {
                    background: url(./resources/postBackground/2.jpg); 
                    background-size: cover; 
                    background-position: center center; 
                    background-repeat: no-repeat;
                }

                &.confess_background_item:nth-of-type(5) {
                    background: url(./resources/postBackground/3.jpg); 
                    background-size: cover; 
                    background-position: center center; 
                    background-repeat: no-repeat;
                }

                &.confess_upload_image_btn {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    width: auto;
                    padding: 0 16px;

                    & .inputfile {
                        width: 0.1px;
                        height: 0.1px;
                        opacity: 0;
                        overflow: hidden;
                        position: absolute;
                        z-index: -1;
                    }

                    .inputfile+label {
                        width: 100%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;

                        & span {
                            font-size: 20px;
                            //color: #233745;
                            color: $-dark-3;
                        }

                        & a {
                            font-size: 13px;
                            margin-left: 8px;
                            color: $-dark-3;
                            margin-top: 2.685px;
                        }
                    }
                }

                @include mobile {
                    width: 100%;
                    height: auto;
                    margin: 0 5px;

                    &.confess_upload_image_btn {
                        display: none;
                    }
                }

                @include tablet {
                    width: 100%;
                    height: auto;
                    margin: 0 5px;

                    &.confess_upload_image_btn {
                        display: none;
                    }
                }

                &:hover {
                    transform: scale(1.075);
                    box-shadow: 0 2px 20px rgba(0, 0, 0, .25);
                }

                &:active {
                    transform: scale(1);
                }

                &.active {
                    border: 2px solid #fff;
                    box-shadow: 0 2px 15px rgba(0, 0, 0, .25);
                }

                &:after {
                    content: "";
                    display: block;
                    padding-bottom: 100%;
                }
            }
        }

        & .btn {
            position: absolute;
            right: 0;
            margin: 0;
            display: flex;
            align-items: center;
            justify-content: center;

            & .btn-icon {
                font-size: 20px;
                margin: 0;
                margin-left: 3px;
            }

            @include mobile {
                position: relative;
                width: 50%;
                margin: auto;
                margin-top: 32px;
                margin-bottom: 24px;
            }

            @include tablet {
                position: relative;
                width: 50%;
                margin: auto;
                margin-top: 32px;
                margin-bottom: 24px;
            }
        }
    }

    & .confess-hastag {
        width: 100%;
        height: auto;
        display: flex;

        & .confess-hastag-icon {
            width: 48px;
            float: left;
            text-align: center;

            & a {
                font-size: 48px;
                font-weight: 600;
                color: #E9B8AC;
            }
        }

        & .confess-hastag-input {
            width: 494px;
            margin-left: 24px;
            border: none;
            float: left;
            font-size: 24px;
            color: #E9B8AC;
            font-weight: 500;
            outline: none;
            border: none;
            transition: all .4s;

            @include mobile {
                width: 150px;
                font-size: 15px;
                margin-left: 9px;
            }

            @include tablet {
                width: 150px;
                font-size: 15px;
                margin-left: 9px;
                color: $-gray-1;
            }

            &::placeholder {
                transition: all .4s;
            }

            &:focus::placeholder {
                color: transparent;
            }
        }
    }
}

.confess-form-success {
    height: 200px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

    & h4 {
        line-height: 55px;
    }
}

