        height: 58px;
        padding-left: 32px;
        padding-right: 32px;
        padding-top: 3px;
        position: fixed;
        color: #fff;
        -webkit-transition: all 0.2s ease;
        -moz-transition: all 0.2s ease;
        -o-transition: all 0.2s ease;
        transition: all 0.2s ease;
        display: flex;
        flex-direction: row;
    
        @include mobile {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            bottom: 0;
            border-top: 1px solid $-white-2;
        }
    
        @include tablet {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            border-top: 1px solid $-white-2;
        }
    
        & .left-menu {
            @include mobile {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                display: none;
    
                & img {
                    margin: 0;
                }
            }
    
            @include tablet {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                display: none;
    
                & img {
                    margin: 0;
                }
            }
    
