                            background-color: transparent;
                            //border-radius: 0;
                            //border-bottom: 3px solid #5b9aeb;
                        }

                        & .nav-icon {
                            line-height: 48px;
                            color: #fff;
                            font-size: 20px;
                            cursor: pointer;
        
                            @include mobile {
                                color: #5b9aeb;
                            }
    
                            @include tablet {
                                color: #5b9aeb;
                            }
                        }
                    }
                    
                    & .nav-icon {
                        line-height: 48px;
                        color: #233745;
                        font-size: 20px;
                        cursor: pointer;
                    }
                }
