        }
    
        & .right-menu {
            float: right;
            width: 250px;
            height: 100%;
            flex: 1;
    
            @include mobile {
                display: none;
    
                & .search-box {
                    width: 96px;
    
                    & input::placeholder {
                        color: transparent;
                    }
                }
            }
    
            @include tablet {
                display: none;
    
                & .search-box {
                    width: 96px;
    
                    & input::placeholder {
                        color: transparent;
                    }
                }
            }
    
            & .search-box {
                width: 250px;
                height: calc(100% - 16px);
                background-color: #f2f4f5;
                margin-top: 6px;
                border-radius: 25px;
                float: right;
                /*border:1px solid #ececec;*/
    
                & .search-icon {
                    color: #6d7176;
                    width: 36px;
                    height: 36px;
                    padding: 7px;
                    padding-left: 10px;
                    float: left;
                }
    
                & input {
                    background: transparent;
                    border: none;
                    width: 200px;
                    height: 37px;
                    padding-bottom: 5px;
                    padding-left: 10px;
                    color: #6d7176;
                    font-weight: 400;
                    font-size: 15px;
                    outline: none;
    
                    &::placeholder {
                        color: #b3b8be;
                    }
                }
            }
    
        }
    }
}

.nav-toggle {
    background-color: rgba(255, 255, 255, .8);
    backdrop-filter: blur(20px) saturate(280%);
    -webkit-backdrop-filter: blur(20px) saturate(280%);
    color: rgba(206, 169, 100, 1);
    box-shadow: 0 2px 4px 0 rgba(182, 182, 182, 0.5);
}

i.material-icons-round.nav-icon.active {
    color: #fff;
    line-height: 40px;
}

input:not([type]):focus:not([readonly]) {
    border: none;
}

input:not([type]):focus:not([readonly]) {
    border: none;
    box-shadow: none;
}

/*FOOTER*/
footer {
    width: 100%;
    height: 64px;
    text-align: center;
    line-height: 64px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px;

    @include mobile {
        margin-bottom: 64px;
    }

    @include tablet {
        margin-bottom: 64px;
    }

    & p {
        color: #9a9da1;
        font-weight: 400;
        font-size: 13px;
        line-height: 1.1;

        & a {
            color: #9a9da1;
            font-weight: 400;
            font-size: 13px;
        }
    }
}

/*CARD*/
.admin-profile {
    height: 400px;
    text-align: center;
    margin: auto;
    max-width: 800px;
    display: -webkit-flex;
    /* Safari */
    -webkit-flex-direction: row-reverse;
    /* Safari 6.1+ */
    display: flex;
    flex-direction: row-reverse;
}

/*CONTAINER*/
.container {
    text-align: center;
}

.section-yellow {
    width: 100%;
    background: #F6EEE8;
    padding: 10px;
    text-align: center;
}

.section-header {
    color: #233745;
    font-weight: 700;
    font-size: 32px;
}

/*PARALLAX*/

.parallax img {
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.bg-header {
    background: url('./resources/image/bg-header.svg') no-repeat;
    background-position: top left;
    height: 85vh;
    background-size: cover;
    display: flex;
    align-items: center;

    @include mobile {
        height: 100vh;
        background-position: -322px;
    }

    & .landing-logo {
        background: url('./resources/image/landingLogo.svg') no-repeat;
        background-position: bottom left;
        height: 116px;
        width: 385px;
        margin-left: 274px;

        @include mobile {
            height: 100%;
            width: 100%;
            margin-left: 10px;
            background-size: 80%;
            background-position: center center;
        }

        @include tablet {
            width: 100%;
            margin: 0;
            background-position: center center;
        }
    }

    & .landing-image {
        float: right;
    }
}

.parallax-text {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    color: #fff;
    position: absolute;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    text-align: center;
}

.parallax-text-container {
    width: 50.666667%;
    margin: auto;
}

.parallax-text-container h1 {
    font-weight: 600;
}

.prl-border {
    border-bottom: 8px solid rgba(206, 160, 100, 1);
    margin-left: auto;
    margin-right: auto;
    width: 100px;
    display: block;
}

/*TEXT*/
.header {
    font-size: 24px;
    font-weight: 700;
    letter-spacing: -.005em;
    line-height: 1.07143;
    color: #233745;
}

h1 {
    font-size: 60px;
    font-style: normal;
    font-weight: 500;
    letter-spacing: -0.5px;
    line-height: 1.26;
}

html,
body {
    margin: 0;
    height: 100%;
    font-family: $-default-font;
    overflow-x: hidden;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
}