.confess_container_parent {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    position: relative;

    & .confess_container {
        width: 80%;
        max-width: 400px;
        height: auto;
        padding: 32px 40px;
        background-color: #fff;
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
        border-radius: 13px;
        margin-top: 48px;
        position: relative;
        transition: all .4s ease;
        -moz-transition: all .4s ease;
    
        &.hasImage,
        &.hasBackground {
            height: 520px;
            box-shadow: none;
            padding: 0;
    
            &.hasBackground {
                min-height: 127.86px;
                height: auto;
                display: flex;
                flex-direction: column;
    
                & .confess_image {
                    height: auto;
                    padding: 32px 40px;
    
                    & .hastag {
                        color: #fff;
                        text-shadow: 0px 0px 10px #000000a8;
                    }
    
                    & .date {
                        color: rgba(255, 255, 255, .95);
                        text-shadow: 0px 0px 10px #000000a8;
    
                        @include mobile {
                            display: none;
                        }
                    }
    
                    & .comments_count {
                        color: rgba(255,255,255,.8);
                        font-weight: 600;
                        font-size: 13px;
                        float: right;
                        margin: 0;
                        margin-top: 32px;
                        position: relative;
                    }
    
                    & .confess {
                        color: #fff;
                        font-size: 18px;
                        font-weight: 500;
                        text-shadow: 0px 0px 10px #000000a8;
                    }
                }
            }
    
            & .confess_image_filter {
                width: 100%;
                height: 100%;
                z-index: 3;
                background: rgba(0, 0, 0, .25);
                position: absolute;
                border-radius: 13px;
    
                & .hastag {
                    color: #fff;
                    margin: 32px 40px 0 40px;
                    text-shadow: 0px 0px 10px #000000a8;
                }
    
                & .date {
                    color: #ffffff85;
                    text-shadow: 0px 0px 10px #000000a8;
    
                    @include mobile {
                        display: none;
                    }
                }
    
                & .comments_count {
                    color: rgba(255,255,255,.8);
                    font-weight: 500;
                    font-size: 13px;
                    margin: 0;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    padding: 20px 40px;
                }
    
                & .confess {
                    position: absolute;
                    max-width: 75%;
                    bottom: 0;
                    color: #fff;
                    font-size: 13px;
                    font-weight: 500;
                    padding: 20px 40px;
                    text-shadow: 0px 0px 10px #000000a8;
                }
            }
    
            & .confess_image {
                width: 100%;
                height: 100%;
                border-radius: 13px;
                z-index: 2;
                position: relative;
            }
    
            & .confess_image_shadow {
                position: absolute;
                width: 70%;
                height: 100%;
                overflow: hidden;
                display: flex;
                left: 50%;
                transform: translate(-50%, 0);
                bottom: -13px;
                z-index: 0;
                border-radius: 20px;
                filter: blur(15px);
    
                @include mobile {
                    width: 80%;
                }
    
                @include tablet {
                    width: 80%;
                }
    
                & .confess_image_shadow_image {
                    margin-top: 3rem;
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                    opacity: .75;
                }
            }
    
            &:hover {
                box-shadow: none;
            }
        }
    
        & .hastag {
            color: $-red;
            font-weight: 600;
            line-height: 1.19048;
            font-size: 21px;
            letter-spacing: .011em;
            margin: 0;
        }
    
        & .confess {
            color: $-black;
            font-weight: 500;
            font-size: 18px;
            margin: 0;
            margin-top: .77em;
            line-height: 1.4211;
            letter-spacing: .012em;
        }
    
        & .comments_count {
            color: #00000075;
            font-weight: 600;
            font-size: 13px;
            float: right;
            margin: 0;
            margin-top: 32px;
        }
    
        & .date {
            position: absolute;
            top: 34px;
            right: 40px;
            margin: 0;
            font-size: 13px;
            font-weight: 600;
            color: #00000075;
            cursor: pointer;
            transition: all .4s;
            -moz-transition: all .4s;
    
            &#dateSecond {
                opacity: 0;
            }
        }
    
        &:hover {
            transform: scale(1.075);
            -moz-transform: scale(1.075);
            box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.15);
        
            @include mobile {
                transform: none;
                -moz-transform: none;
            } 
        
            @include tablet {
                transform: none;
                -moz-transform: none;
            }
        }
    }
}